  public show_image_report_sales_category: any = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').show_image_report_sales_category;

  // doanh số KH report_sales_customer
  public show_btax_report_sales_customer: any = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_customer;

