
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ConfigurationComponent } from './configuration.component';
import { CurrencyComponent } from './currency/currency.component';
import { DisplayComponent } from './display/display.component';
import { ReportComponent } from './report/report.component';
import { SalesComponent } from './sales/sales.component';
import { Routes, RouterModule } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { InvoicesComponent } from './invoices/invoices.component';
import { SyncDataComponent } from './sync-data/sync-data.component';

import { LedPriceComponent } from './led-price/led-price.component';
import { ProductsComponent } from './products/products.component';
const routes: Routes = [
  {
    path: '',
    component: ConfigurationComponent
  },
  {
    path: 'theme',
    component: DisplayComponent
  },
  {
    path: 'sale-report',
    component: ReportComponent
  },
  {
    path: 'invoices',
    component: InvoicesComponent
  },
  {
    path: 'sync-data',
    component: SyncDataComponent
  },
  {
    path: 'led-price',
    component: LedPriceComponent
  },
  {
    path: 'sales-program',
    loadChildren: () => import('./sales-program/sales-program.module').then(m => m.SalesProgramModule)
  },
  {
    path: 'products',
    component: ProductsComponent
  },
]

@NgModule({
  declarations: [
    ConfigurationComponent,
    CurrencyComponent,
    DisplayComponent, ReportComponent, SalesComponent, InvoicesComponent, SyncDataComponent, LedPriceComponent, ProductsComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule,
  ]
})
export class ConfigurationModule { }
