import { VhComponent } from './../../../../components/vh-component/vh-component';
import { VhQuerySales } from 'ionic-vhframeworks'; 
import { Component, NgZone, OnInit } from '@angular/core';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-sync-data',
  templateUrl: './sync-data.component.html',
  styleUrls: ['./sync-data.component.scss']
})
export class SyncDataComponent implements OnInit {
  finishSync = false
  startSync = false;
  percent = 0
  constructor(
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private ls:LanguageService
  ) { }

  ngOnInit(): void {
  } 
  /**
   * đồng bộ dữ liệu
   */
  syncData() {
    this.percent = 0
    this.startSync = true;
    this.vhQuerySales.makeCorrectQuantityProducts_byShop()
      .then((bool) => {
        
        this.vhComponent.alertMessageDesktop("success",this.ls.translate("Đồng bộ thành công"))
        this.finishSync = true
        this.startSync = false
        this.percent = 100
        clearInterval(interval)
      })
    let interval = setInterval(() => {
      if (this.percent < 99) this.percent++;
    }, 500);
  }
}
